.form-parent {
    position: relative;
    border: 1px solid transparent;
    border-bottom: 1px solid #000;
    transition: all ease 0.5s;

    .form-toolbar {
        // position: absolute;
        float: right;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 8px;
        border: 1px solid #7d86a5;
        border-radius: 4px;
        width: 80px;
        height: 24px;
        background: #fff;
        color:#444;
        i{
            cursor: pointer;
            &:hover{
                color:#bd361e;
            }
        }

        &.d-top-right {
            // top: 5px;
            // right: 5px;
        }
    }

    &:hover {
        border-color: rgb(81, 93, 201) !important;
    }

}